<template>
  <v-hover
      v-slot:default="{ hover }"
      disabled
  >
    <v-card
        :elevation="hover ? 20 : 2"
        width="263"
        class="ml-9 mt-4"
        :dark="dark"
        outlined
        rounded="xl"
        img=""
    >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title style="font-size: 17px; display: flex; justify-content: space-between;">
            <span style="overflow: hidden; text-overflow: ellipsis;">{{ $attrs.cardtitle }}</span>
            <v-dialog v-model="addNoteDialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                    fab
                    v-show="!showImg"
                    icon
                    small
                    :title="$t('card.note.addNote')"
                    v-bind="attrs"
                    v-on="on"
                    height="20"
                >
                  <v-icon size="20">mdi-plus</v-icon>
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  {{ $t('card.note.addNote') }}
                </v-card-title>
                <v-card-text>
                  <ValidationObserver
                      ref="obs"
                      v-slot="{ validated }"
                  >
                    <v-text-field-with-validation
                        :validated="validated"
                        rules="required"
                        placeholder="输入笔记标题"
                        v-model="newNoteInfo.title"
                    />
                    <v-textarea
                        solo
                        v-model="newNoteInfo.content"
                    />
                  </ValidationObserver>
                </v-card-text>
                <v-card-actions>
                  <v-spacer/>
                  <v-btn color="blue darken-1" text @click="dialogClose">{{ $t('appBar.dialogClose') }}</v-btn>
                  <v-btn color="blue darken-1" text @click="addNote">{{ $t('appBar.dialogAdd') }}</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider/>

      <a target="_blank" :href="$attrs.url" rel="noopener">
        <v-img
            v-show="showImg"
            src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1730713693,2130926401&fm=26&gp=0.jpg"
            height="194"
        />
      </a>

      <v-virtual-scroll
          v-show="!showImg"
          :items="noteOfBookmark"
          item-height="35"
          height="194"
      >
        <template v-slot="{ item, index }">
          <v-list-item>
            <v-list-item-content
                @click="showNote(item.title, item.content, item.id, index)"
            >
              <v-list-item-title style="font-size:15px;">{{ item.title }}</v-list-item-title>
            </v-list-item-content>

            <v-list-item-action>
              <v-spacer/>
              <v-btn
                  icon
                  small
                  :title="$t('card.note.delete')"
                  @click="deleteNote( item.id, index )"
              >
                <v-icon color="purple">mdi-trash-can-outline</v-icon>
              </v-btn>
            </v-list-item-action>
          </v-list-item>
        </template>
      </v-virtual-scroll>

      <v-divider/>

      <v-dialog v-model="showNoteDialog" max-width="700px">
        <v-card>
          <v-card-text>
            <div v-show="!showNoteEdit">
              <v-card-title style="font-size: 25px;">
                {{ showAndUpdateNoteInfo.title }}
              </v-card-title>
              <v-card-text style="font-size:17px; color: black; height: 480px; overflow-y:scroll; line-height: 1.75rem;">
                {{ showAndUpdateNoteInfo.content }}
              </v-card-text>
            </div>
            <div v-show="showNoteEdit">
              <v-text-field v-model="showAndUpdateNoteInfo.title" style="font-size: 25px;"/>
              <v-textarea outlined v-model="showAndUpdateNoteInfo.content" rows="15" style="font-size:17px; color: black;"/>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn color="blue darken-1" text @click="noteEdit">{{ noteEditIcon }}</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="bookmarkEditDialog" max-width="700px">
        <v-card>
          <v-card-title>
            编辑书签
          </v-card-title>
          <v-card-text class="pt-6">
            <ValidationObserver
                ref="obs"
                v-slot="{ validated }"
            >
              <VTextFieldWithValidation
                  :validated="validated"
                  v-model="updateBookmarkInfo.bookmarkInfo.title"
                  rules="required"
                  :label="$t('appBar.bookmarkTitle')"
              />
              <VTextFieldWithValidation
                  :validated="validated"
                  v-model="updateBookmarkInfo.bookmarkInfo.url"
                  rules="required"
                  :label="$t('appBar.bookmarkUrl')"
              />
            </ValidationObserver>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="updBmConfirm">确定</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-card-text>
        收藏时间：{{ desc }}
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn
            icon
            small
            depressed
            v-for="item in cardIcons"
            :key="item.title"
            link
        >
          <v-icon
              :title="item.title"
              @click="iconMethod(item.method)"
              :color="$attrs.top ? item.color : ''">{{ item.icon }}
          </v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-hover>
</template>

<script>
import {ValidationObserver} from "vee-validate";
import VTextFieldWithValidation from "@/views/components/VTextFieldWithValidation";

export default {
  components: {
    ValidationObserver,
    VTextFieldWithValidation,
  },

  props: {
    value: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    },
    desc: {
      type: undefined,
      default: "desc"
    }
  },

  data: () => ({
    noteEditClickTimes: 0,
    noteEditIcon: '编辑',
    topIconColor: 'purple',
    addNoteDialog: false,
    showNoteDialog: false,
    bookmarkEditDialog: false,
    showNoteEdit: false,
    showImg: true,
    noteBtnClick: 0,
    delNoteArr: {
      noteIds: []
    },
    bookmarkParam: {
      bookmarkArr: {
        bookmarkIds: [],
      },
      respAndIndex: {
        index: 0,
        resp: 0,
        data: {}
      }
    },
    noteOfBookmark: [],
    newNoteInfo: {
      parentId: '',
      content: '',
      title: ''
    },
    showAndUpdateNoteInfo: {
      parentId: '',
      content: '',
      id: '',
      title: '',
      index: 0
    },
    updateBookmarkInfo: {
      bookmarkInfo: {
        description: "",
        id: 0,
        tags: [],
        title: "",
        url: ""
      },
      respAndIndex: {
        resp: {},
        index: 0,
      }
    },
  }),

  computed: {
    cardIcons() {
      return [
        {title: this.$t('card.bookmark.icon.note'), icon: "mdi-calendar-text", method: "bookmarkNote"},
        {title: this.$t('card.bookmark.icon.edit'), icon: "mdi-square-edit-outline", method: "bookmarkEdit"},
        // {title: this.$t('card.bookmark.icon.qrCode'), icon: "mdi-qrcode", method: "test"},
        {title: this.$t('card.bookmark.icon.top'), icon: "mdi-format-align-top", method: "top", color: this.topIconColor},
        {title: this.$t('card.bookmark.icon.delete'), icon: "mdi-trash-can-outline", method: 'delete'}
      ]
    }
  },

  methods: {
    updBmConfirm() {
      this.$store.dispatch('updateBookmarkInfo', this.updateBookmarkInfo).then(() => {
        this.bookmarkEditDialog = !this.bookmarkEditDialog
      })
    },
    noteEdit() {
      this.showNoteEdit = !this.showNoteEdit
      this.noteEditClickTimes++
      if (this.noteEditClickTimes % 2 === 1) {
        this.noteEditIcon = '确认'
      }
      if (this.noteEditClickTimes % 2 === 0) {
        this.showAndUpdateNoteInfo.parentId = this.$attrs.id
        this.$store.dispatch('updateNoteInfo', this.showAndUpdateNoteInfo).then(() => {
          this.noteOfBookmark.splice(this.showAndUpdateNoteInfo.index, 1, Object.assign({}, this.showAndUpdateNoteInfo))
          this.noteEditIcon = '编辑'
        })
      }

    },
    showNote(title, content, noteId, index) {
      this.showNoteDialog = true
      this.showAndUpdateNoteInfo.title = title
      this.showAndUpdateNoteInfo.content = content
      this.showAndUpdateNoteInfo.id = noteId
      this.showAndUpdateNoteInfo.index = index

    },
    dialogClose() {
      this.newNoteInfo.title = ''
      this.newNoteInfo.content = ''
      this.$refs.obs.reset()
      this.addNoteDialog = false
    },
    addNote() {
      this.newNoteInfo.parentId = this.$attrs.id
      this.$refs.obs.validate().then(isValidate => {
        if (isValidate) {
          this.$store.dispatch('addNote', this.newNoteInfo).then(resp => {
            this.noteOfBookmark.unshift(resp)
            this.newNoteInfo.title = ''
            this.newNoteInfo.content = ''
            this.$refs.obs.reset()
            this.addNoteDialog = false
          })
        }
      })
    },
    deleteNote(id, index) {
      this.delNoteArr.noteIds = []
      this.delNoteArr.noteIds.push(id)
      this.$store.dispatch('deleteNoteOfBookmark', this.delNoteArr)
      this.noteOfBookmark.splice(index, 1)
    },
    iconMethod(method) {
      this[method]()
    },
    bookmarkNote() {
      if (this.noteBtnClick === 0) {
        this.$store.dispatch('getNoteOfBookmark', this.$attrs.id).then(resp => {
          this.noteOfBookmark = resp
        })
        this.noteBtnClick++
      }
      this.showImg = !this.showImg
    },
    bookmarkEdit() {
      this.updateBookmarkInfo.bookmarkInfo.url = this.$attrs.url
      this.updateBookmarkInfo.bookmarkInfo.title = this.$attrs.cardtitle
      this.updateBookmarkInfo.bookmarkInfo.id = this.$attrs.id
      this.updateBookmarkInfo.respAndIndex.index = this.$attrs.index
      this.bookmarkEditDialog = !this.bookmarkEditDialog
    },
    delete() {
      this.bookmarkParam.bookmarkArr.bookmarkIds = []
      this.bookmarkParam.bookmarkArr.bookmarkIds.push(this.$attrs.id)
      this.bookmarkParam.respAndIndex.index = this.$attrs.index
      this.$store.dispatch('deleteBookmark', this.bookmarkParam)
    },
    top() {
      this.bookmarkParam.bookmarkArr.bookmarkIds = []
      this.bookmarkParam.bookmarkArr.bookmarkIds.push(this.$attrs.id)
      this.bookmarkParam.respAndIndex.index = this.$attrs.index
      if (this.$attrs.top === false) {
        this.$store.dispatch('bookmarkTop', this.bookmarkParam)
      } else {
        this.$store.dispatch('bookmarkTopCancel', this.bookmarkParam)
      }
    },
  }
}
</script>